
.vue-preload-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: table;
  text-align: center;
}
.vue-preload-image .progress {
    display: table-cell;
    vertical-align: middle;
}
.vue-preload-image .progress p {
      margin: 0;
}
.vue-preload-image .progress .tip-str {
      margin-top: .4em;
}
.vue-preload-image .progress .loadingAnimation {
      width: 60px;
      height: 60px;
      background-color: #67CF22;
      margin: 0 auto;
      animation: rotateplane 1.2s infinite ease-in-out;
}
@keyframes rotateplane {
0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
}
50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
}
100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
}
}



